.chaincode-wrapper {
  background: white;
  padding: 40px 40px 40px; }
  .chaincode-wrapper .button-warning {
    background-color: #fff;
    border-color: #ff3000;
    color: #ff3000; }
  .chaincode-wrapper .button-warning:hover {
    background-color: #ff3000;
    border-color: #ff3000;
    color: #fff; }
  .chaincode-wrapper .button-start {
    background-color: #fff;
    border-color: #669933;
    color: #669933; }
  .chaincode-wrapper .button-start:hover {
    background-color: #006633;
    border-color: #006633;
    color: #fff; }



