.monitor-wrapper {
  background: white;
  padding: 40px 40px 40px; }
  .monitor-wrapper .monitor-header {
    padding: 10px 0; }
    .monitor-wrapper .monitor-header .monitor-title {
      display: inline-block;
      font-size: 15px;
      margin-right: 20px; }
      .monitor-wrapper .monitor-header .monitor-title .monitor-subtitle {
        font-size: 10px;
        display: inline-block; }
      .monitor-wrapper .monitor-header .monitor-title .monitor-subtitle-run {
        color: #669933; }
      .monitor-wrapper .monitor-header .monitor-title .monitor-subtitle-stop {
        color: #8c8c8c; }
      .monitor-wrapper .monitor-header .monitor-title .monitor-subtitle-build {
        color: #CC6600; }
      .monitor-wrapper .monitor-header .monitor-title .monitor-subtitle-error {
        color: #ff3000; }
    .monitor-wrapper .monitor-header .button-start {
      background-color: #fff;
      border-color: #669933;
      color: #669933; }
    .monitor-wrapper .monitor-header .button-start:hover {
      background-color: #006633;
      border-color: #006633;
      color: #fff; }
    .monitor-wrapper .monitor-header .button-stop {
      background-color: #fff;
      border-color: #ff3000;
      color: #ff3000; }
    .monitor-wrapper .monitor-header .button-stop:hover {
      background-color: #ff3000;
      border-color: #ff3000;
      color: #fff; }
  .monitor-wrapper .monitor-container {
    padding: 0 0 20px; }
    .monitor-wrapper .monitor-container .feedback {
      margin: 1px 0; }
    .monitor-wrapper .monitor-container .feedback-loading {
      background: #8c8c8c; }
  .monitor-wrapper .monitor-selection {
    margin: 5px 0 20px; }
    .monitor-wrapper .monitor-selection .monitor-card {
      width: 100%; }
      .monitor-wrapper .monitor-selection .monitor-card .monitor-button {
        width: 100%;
        border-radius: 0;
        margin-top: 20px; }
      .monitor-wrapper .monitor-selection .monitor-card .number {
        padding-top: 50px;
        font-size: 70px; }
  .monitor-wrapper .monitor-block {
    margin: 20px 0; }
    .monitor-wrapper .monitor-block .monitor-card {
      width: 90%;
      margin: 0 5% 0; }



