.baas-wrapper {
  background: white;
  padding: 40px 40px 40px; }
  .baas-wrapper .tag-run {
    color: #669933;
    background-color: #beeee0; }
  .baas-wrapper .tag-stop {
    color: #000;
    background-color: #CCCCCC; }
  .baas-wrapper .tag-error {
    color: #CC0033;
    background-color: #FFCCCC; }
  .baas-wrapper .tag-build {
    color: #CC6600;
    background-color: #FFFF66; }
  .baas-wrapper .button-warning {
    background-color: #fff;
    border-color: #ff3000;
    color: #ff3000; }
  .baas-wrapper .button-warning:hover {
    background-color: #ff3000;
    border-color: #ff3000;
    color: #fff; }
  .baas-wrapper .button-start {
    background-color: #fff;
    border-color: #669933;
    color: #669933; }
  .baas-wrapper .button-start:hover {
    background-color: #006633;
    border-color: #006633;
    color: #fff; }



