/* for example java annotations */
.sonarqube-code {
  display: grid;
  grid-template-columns: 50px 1fr;
  /* constants */
  /* javadoc */
  /* classic comment */
  /* C++ doc */
  /* keyword */
  /* string */
  /* keyword light*/
  /* preprocessing directive */ }
  .sonarqube-code .sonarqube-code__issue {
    grid-column: span 2;
    background-color: #fcf3f3;
    color: #9b5656;
    margin-bottom: 5px; }
    .sonarqube-code .sonarqube-code__issue .ant-descriptions-item-content {
      color: inherit; }
  .sonarqube-code__line {
    padding: 0 7px; }
  .sonarqube-code .--issue {
    background-color: #f3cfd0; }
  .sonarqube-code .a {
    color: #808000; }
  .sonarqube-code .c {
    color: #660e80;
    font-style: normal;
    font-weight: bold; }
  .sonarqube-code .j {
    color: #666666;
    font-style: normal; }
  .sonarqube-code .cd {
    color: #666666;
    font-style: italic; }
  .sonarqube-code .cppd {
    color: #666666;
    font-style: italic; }
  .sonarqube-code .k {
    color: #0071ba;
    font-weight: 600; }
  .sonarqube-code .s {
    color: #277b31;
    font-weight: normal; }
  .sonarqube-code .h {
    color: #000080;
    font-weight: normal; }
  .sonarqube-code .p {
    color: #347235;
    font-weight: normal; }



