.loading-new-project {
  width: 100%; }

.new-project-container {
  background-color: white; }

.div-new-project-top {
  font-weight: bold;
  font-size: 17px;
  line-height: 60px;
  height: 60px;
  padding-left: 20px;
  border-bottom: 1px solid #e8e8e8;
  margin-bottom: 15px; }

.div-new-project-input {
  padding: 15px 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center; }

.div-new-project-visibility {
  width: 85%; }

.label-new-project-left {
  width: 15%;
