  height: 40px;
  border-radius: 2px;
  margin-left: 20px;
  font-size: 13px; }

.btn-new-project-cancel {
  margin-top: 20px;
  margin-right: 20px;
  float: right;
  background-color: white;
  border-color: #e2e2e2;
