.mr-list-container {
  background-color: white;
  min-height: 600px; }

.loading-mr-list {
  width: 100%;
  min-height: 500px; }

.div-mr-list-top {
  height: 60px;
  border-bottom: 1px solid #d1d1d1; }

.btn-new-mr {
  font-size: 13px;
  height: 34px;
  padding: 0 15px;
  float: right;
  border-radius: 3px;
  background-color: #1aaa55;
  border-color: #168f48;
  color: #fff;
  margin: 13px 15px 13px 0; }

.div-mr-list-top ul {
  display: inline-block; }

.div-mr-list-top li {
  list-style: none;
  padding: 0;
  margin: 0;
  float: left; }

.mr-list-menu-item {
  display: block;
  width: 90px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  text-decoration: none; }

.mr-list-menu-item-active {
  display: block;
  width: 90px;
