.mr-list-menu-item span {
  color: #8a8a8a;
  font-size: 15px;
  font-weight: bold; }

.mr-list-menu-item-active span {
  color: black;
  font-size: 15px;
  font-weight: bold; }

.mr-list-item {
  padding: 0 20px;
  height: 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #f3f3f3; }

.mr-list-item-center {
  width: 100%; }

.mr-list-item-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 23px;
  font-weight: bold;
  font-size: 15px; }

.mr-list-item-time {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 23px; }

.text-list-time-left {
  display: inline-block;
  width: 80%;
  text-align: left; }

.text-list-time-right {
  display: inline-block;
  width: 20%;
  text-align: right; }



