.file-container {
  background-color: white;
  min-height: 600px;
  position: relative; }

.div-tree-top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 80px;
  padding-left: 20px;
  position: relative;
  background-color: #fbfbfb; }

.btn-search-file {
  position: absolute;
  border: 1px solid #d6d6d6;
  height: 30px;
  top: 25px;
  right: 30px;
  border-radius: 5px; }

.btn-search-file:hover {
  background-color: #e8e8e8; }

.img-tree-search {
  width: 13px;
  height: 13px;
  margin-right: 5px;
  vertical-align: middle; }

.text-tree-search {
  vertical-align: middle;
  font-size: 13px; }

.div-tree-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 50px;
  line-height: 50px;
  padding: 0 15px;
  border-top: 1px solid #dce0e6;
  border-bottom: 1px solid #dce0e6;
  background-color: #f7f8fa; }

.div-tree-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 50px;
  line-height: 50px;
  padding: 0 15px;
  border-bottom: 1px solid #dce0e6; }

.div-tree-item:hover {
  background-color: #edfdff; }

.div-tree-content {
  border-radius: 5px;
  overflow: hidden;
  margin-top: 20px;
  border: 1px solid #d6d6d6; }

.text-tree-title {
  /*font-weight: bold;*/ }

.text-tree-name-title {
  display: inline-block;
  width: 38%;
  font-size: 15px;
  /*font-weight: bold;*/ }

.text-tree-commit-title {
  display: inline-block;
  width: 50%;
  font-size: 15px;
  /*font-weight: bold;*/ }

.text-tree-update-title {
  display: inline-block;
  width: 10%;
