  /*font-weight: bold;*/ }

.text-tree-name {
  display: inline-block;
  width: 38%;
  font-size: 15px; }

.text-tree-commit {
  display: inline-block;
  width: 45%;
  font-size: 15px;
  color: #848484; }

.text-tree-update {
  display: inline-block;
  width: 15%;
