  font-size: 15px;
  color: #585858; }

.loading-tree-update {
  width: 3%; }

.text-tree-update-loading {
  display: inline-block;
  width: 12%;
  font-size: 15px;
  color: #585858; }

.img-tree-folder {
  width: 15px;
  height: 12px;
  margin-right: 5px; }

.img-tree-file {
  width: 15px;
  height: 15px;
  margin-right: 5px; }

.text-tree-separator {
  color: #b1b1b1; }

a:hover {
  cursor: pointer;
  color: black; }

.loading-ref-tree {
  margin-right: 20px; }

.select-ref-tree {
  width: 200px; }



