@charset "UTF-8";
.diff-decoration {
  line-height: 2;
  font-family: SFMono-Regular, Consolas, "Liberation Mono", Menlo, Courier, monospace;
  background-color: #f2f8ff; }

.diff-decoration-content {
  background-color: #f2f8ff;
  padding-left: .5em;
  color: #999; }

.diff-line-hover-old.diff-gutter,
.diff-line-hover-new.diff-gutter,
.diff-unified .diff-line:hover .diff-gutter {
  font-size: 0;
  text-align: center;
  vertical-align: middle; }

.diff-line-hover-old.diff-gutter::before,
.diff-line-hover-new.diff-gutter::before,
.diff-unified .diff-line:hover .diff-gutter::before {
  content: "";
  font-size: 14px;
  font-weight: bold; }

.diff {
  table-layout: fixed;
  border-collapse: collapse;
  width: 100%; }

.diff-gutter-col {
  width: 7ch; }

.diff-gutter-omit {
  height: 0; }

.diff-gutter-omit::before {
  content: " ";
  display: block;
  white-space: pre;
  width: 2px;
  height: 100%;
  margin-left: 4.6ch;
  overflow: hidden;
  background-color: #cb2a1d; }

.diff td {
  vertical-align: top;
  padding-top: 0;
  padding-bottom: 0; }

.diff-line {
  line-height: 1.5;
  font-family: Consolas, Courier, monospace; }

.diff-gutter > a {
  color: inherit;
  display: block; }

.diff-gutter,
.diff-gutter > a {
  padding: 0 1ch;
  text-align: right;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.diff-gutter-insert {
  background-color: #d6fedb; }

.diff-gutter-delete {
  background-color: #fadde0; }

.diff-gutter-omit {
  cursor: default; }

.diff-gutter-selected {
  background-color: #fef6b9; }

.diff-code {
  white-space: pre-wrap;
  word-wrap: break-word;
  word-break: break-all;
  padding: 0;
  padding-left: .5em; }

.diff-code-insert {
  background-color: #eaffee; }

.diff-code-edit {
  display: inline-block;
  color: inherit; }

.diff-code-insert .diff-code-edit {
  background-color: #c0dc91; }

.diff-code-delete {
  background-color: #fdeff0; }

.diff-code-delete .diff-code-edit {
  background-color: #f39ea2; }

.diff-code-selected {
  background-color: #fffce0; }

.diff-omit {
  background-color: #fafbfc; }



